// src/scss/components/_navbar.scss
@use '../abstracts/variables' as vars;
@use '../abstracts/mixins' as mix;
@use '../abstracts/functions' as fn;
@use 'sass:color';

.navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: fn.z('fixed');
    padding: vars.$spacing-md 0;
    transition: all 0.3s ease;
    background-color: color.adjust(vars.$primary-color, $alpha: -0.1);
    backdrop-filter: blur(10px);
  
    &.scrolled {
      padding: vars.$spacing-sm 0;
      background-color: color.adjust(vars.$primary-color, $alpha: -0.05);
      box-shadow: 0 2px 10px color.adjust(vars.$dark-color, $alpha: -0.9);
    }
  
    // Brand
    &-brand {
      display: flex;
      align-items: center;
      font-family: vars.$font-primary;
      font-size: 1.8rem;
      font-weight: vars.$font-weight-bold;
      color: vars.$secondary-color;
      text-decoration: none;
      transition: vars.$transition-base;
  
      img {
        width: 40px;
        height: 40px;
        margin-right: vars.$spacing-sm;
        object-fit: contain;
      }
  
      &:hover {
        color: color.adjust(vars.$secondary-color, $lightness: 10%);
        transform: translateY(-1px);
      }
    }
  
    // Navigation
    &-nav {
      margin: 0 auto;
      display: flex;
      align-items: center;
      gap: vars.$spacing-md;
  
      .nav-item {
        position: relative;
  
        .nav-link {
          color: vars.$light-color;
          font-size: vars.$font-size-base;
          font-weight: vars.$font-weight-normal;
          text-transform: uppercase;
          letter-spacing: 1px;
          padding: vars.$spacing-xs vars.$spacing-sm;
          transition: vars.$transition-base;
  
          &:hover,
          &.active {
            color: vars.$secondary-color;
          }
  
          &::after {
            content: '';
            position: absolute;
            bottom: -2px;
            left: 50%;
            width: 0;
            height: 2px;
            background-color: vars.$secondary-color;
            transition: all 0.3s ease;
            transform: translateX(-50%);
          }
  
          &:hover::after,
          &.active::after {
            width: 100%;
          }
        }
  
        // Dropdown
        .dropdown-menu {
          margin-top: vars.$spacing-sm;
          padding: vars.$spacing-xs 0;
          background-color: vars.$light-color;
          border: none;
          border-radius: vars.$border-radius;
          box-shadow: vars.$box-shadow;
  
          .dropdown-item {
            padding: vars.$spacing-xs vars.$spacing-md;
            color: vars.$text-color;
            font-size: vars.$font-size-sm;
            transition: vars.$transition-base;
  
            &:hover,
            &:focus {
              background-color: color.adjust(vars.$secondary-color, $alpha: -0.9);
              color: vars.$secondary-color;
            }
  
            &.active {
              background-color: vars.$secondary-color;
              color: vars.$light-color;
            }
          }
        }
      }
    }
  
    // Action buttons
    .navbar-actions {
      display: flex;
      align-items: center;
      gap: vars.$spacing-sm;
  
      .btn {
        padding: vars.$spacing-xs vars.$spacing-md;
        font-size: vars.$font-size-sm;
        text-transform: uppercase;
        letter-spacing: 1px;
      }
    }
  
    // Toggler
    .navbar-toggler {
      border: none;
      padding: vars.$spacing-xs;
      color: vars.$light-color;
      transition: vars.$transition-base;
  
      &:focus {
        outline: none;
        box-shadow: none;
      }
  
      &-icon {
        width: 24px;
        height: 2px;
        background-color: currentColor;
        position: relative;
        transition: vars.$transition-base;
  
        &::before,
        &::after {
          content: '';
          position: absolute;
          width: 24px;
          height: 2px;
          background-color: currentColor;
          transition: vars.$transition-base;
        }
  
        &::before {
          top: -6px;
        }
  
        &::after {
          bottom: -6px;
        }
      }
  
      &[aria-expanded="true"] {
        .navbar-toggler-icon {
          background-color: transparent;
  
          &::before {
            transform: translateY(6px) rotate(45deg);
          }
  
          &::after {
            transform: translateY(-6px) rotate(-45deg);
          }
        }
      }
    }
}
  
// Responsive
@include mix.respond-to(md) {
    .navbar {
      &-nav {
        .nav-item {
          .nav-link {
            font-size: vars.$font-size-sm;
            padding: vars.$spacing-xs;
          }
        }
      }
  
      .navbar-actions {
        .btn {
          padding: vars.$spacing-xs vars.$spacing-sm;
        }
      }
    }
}
  
@include mix.respond-to(sm) {
    .navbar {
      padding: vars.$spacing-sm 0;
  
      &-brand {
        font-size: 1.5rem;
  
        img {
          width: 32px;
          height: 32px;
        }
      }
  
      &-collapse {
        background-color: color.adjust(vars.$primary-color, $alpha: -0.05);
        padding: vars.$spacing-md;
        margin: 0 -#{vars.$spacing-md};
        border-radius: vars.$border-radius;
  
        .navbar-nav {
          gap: vars.$spacing-sm;
  
          .nav-item {
            width: 100%;
            text-align: center;
  
            .nav-link {
              padding: vars.$spacing-sm;
              
              &::after {
                display: none;
              }
            }
  
            .dropdown-menu {
              background-color: transparent;
              box-shadow: none;
              text-align: center;
  
              .dropdown-item {
                color: vars.$light-color;
  
                &:hover {
                  background-color: color.adjust(vars.$light-color, $alpha: -0.9);
                }
              }
            }
          }
        }
      }
    }
}