.header-hero {
  height: 100vh;
  min-height: 600px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #f8f9fa;
  overflow: hidden;
}
.header-hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(rgba(52, 58, 64, 0.4), rgba(52, 58, 64, 0.6)), url("/src/assets/images/backgrounds/hero-bg.jpg") no-repeat center center;
  background-size: cover;
  z-index: -1;
}
.header-hero-content {
  max-width: 800px;
  padding: 1.5rem;
  animation: fadeInUp 1s ease;
}
.header-hero-content h1 {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(52, 58, 64, 0.3);
}
@media (min-width: 720px) {
  .header-hero-content h1 {
    font-size: 2.5rem;
  }
}
@media (min-width: 540px) {
  .header-hero-content h1 {
    font-size: 2rem;
  }
}
.header-hero-content p {
  font-size: 1.25rem;
  margin-bottom: 1.5rem;
  text-shadow: 1px 1px 2px rgba(52, 58, 64, 0.3);
}
@media (min-width: 540px) {
  .header-hero-content p {
    font-size: 1rem;
  }
}
.header-hero-content .btn {
  padding: 0.5rem 2rem;
  font-size: 1.25rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}
@media (min-width: 540px) {
  .header-hero-content .btn {
    padding: 0.5rem 1.5rem;
    font-size: 1rem;
  }
}
.header-hero-scroll {
  position: absolute;
  bottom: 1.5rem;
  left: 50%;
  transform: translateX(-50%);
  animation: bounce 2s infinite;
}
.header-hero-scroll i {
  color: #f8f9fa;
  font-size: 2rem;
}
.header-page {
  height: 300px;
  background: linear-gradient(rgba(52, 58, 64, 0.6), rgba(52, 58, 64, 0.6)), url("/src/assets/images/backgrounds/page-header.jpg") no-repeat center center;
  background-size: cover;
  display: flex;
  align-items: center;
