  color: rgba(248, 249, 250, 0.8);
}
.header-page-content .breadcrumb .breadcrumb-item + .breadcrumb-item::before {
  color: rgba(248, 249, 250, 0.8);
}
.header-page-content .breadcrumb .breadcrumb-item.active {
  color: #f8f9fa;
}
.header-page-content .breadcrumb .breadcrumb-item a {
  color: #d4af37;
  text-decoration: none;
