.login-container {
  min-height: 100vh;
  padding-top: 5rem;
  background-color: #f8f9fa;
}

.auth-wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0;
  min-height: calc(100vh - 5rem);
}
@media (min-width: 720px) {
  .auth-wrapper {
    grid-template-columns: 1fr;
  }
}

.auth-side .auth-image {
  position: relative;
  height: 100%;
  min-height: 18.75rem;
  background: url("/src/assets/images/backgrounds/login-bg.avif") center/cover no-repeat;
}
.auth-side .auth-image .overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(45deg, rgba(26, 26, 26, 0.7), rgba(212, 175, 55, 0.3));
}
.auth-side .auth-image .auth-quote {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #f8f9fa;
  z-index: z("fixed");
  width: 80%;
}
.auth-side .auth-image .auth-quote h2 {
  font-family: "Playfair Display", serif;
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 1rem;
}
.auth-side .auth-image .auth-quote p {
  font-size: 1.125rem;
  font-family: "Roboto", sans-serif;
}

.auth-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  padding: 2.5rem;
  background-color: #f8f9fa;
}

.auth-form-container {
  width: 100%;
  max-width: 30rem;
}

.auth-header {
  text-align: center;
  margin-bottom: 2.5rem;
}
.auth-header h1 {
  font-family: "Playfair Display", serif;
  font-size: 2rem;
  font-weight: 700;
  line-height: 1.2;
  color: #1a1a1a;
  margin-bottom: 0.5rem;
}
.auth-header p {
  color: rgb(127.5, 127.5, 127.5);
}

.auth-form .form-group {
  margin-bottom: 1.5rem;
}
.auth-form .form-group label {
  display: block;
  color: #1a1a1a;
  font-weight: 700;
  margin-bottom: 0.5rem;
}
.auth-form .input-wrapper {
  position: relative;
}
.auth-form .input-wrapper i {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  color: #8a6d3b;
}
.auth-form .input-wrapper .form-input {
  width: 100%;
  padding: 0.75rem 0.75rem 0.75rem 2.75rem;
  border: 1px solid hsl(210, 10.3448275862%, 102.7450980392%);
  border-radius: 4px;
  font-size: 1rem;
  transition: all 0.3s ease;
}
.auth-form .input-wrapper .form-input:focus {
  outline: none;
  border-color: #d4af37;
  box-shadow: 0 0 0 2px hsl(45.8598726115, 64.6090534979%, 137.3529411765%);
}
.auth-form .input-wrapper .password-toggle {
  display: inline-block;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  font-weight: 700;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  outline: none;
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  padding: 0.25rem;
  color: #8a6d3b;
  background: none;
}
.auth-form .input-wrapper .password-toggle:disabled {
  opacity: 0.65;
  cursor: not-allowed;
}
.auth-form .input-wrapper .password-toggle:hover {
  color: #d4af37;
}

.form-options {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  margin-bottom: 1.5rem;
}
.form-options .remember-me {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: nowrap;
  gap: 0.5rem;
  color: #333;
  cursor: pointer;
}
.form-options .remember-me input[type=checkbox] {
  width: 1rem;
  height: 1rem;
}
.form-options .forgot-password {
  color: #8a6d3b;
  text-decoration: none;
  font-size: 0.875rem;
}
.form-options .forgot-password:hover {
  color: #d4af37;
  text-decoration: underline;
}

.btn-login {
  display: inline-block;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  font-weight: 700;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  outline: none;
  background-color: #d4af37;
  color: #f8f9fa;
  width: 100%;
  padding: 0.75rem;
  font-size: 1rem;
  margin-bottom: 1.5rem;
}
.btn-login:disabled {
  opacity: 0.65;
  cursor: not-allowed;
}
.btn-login:hover, .btn-login:focus {
  background-color: rgb(177.7777777778, 144.8888888889, 38.2222222222);
}

.social-login .divider {
  display: block;
  text-align: center;
  color: rgb(127.5, 127.5, 127.5);
  margin-bottom: 1.5rem;
  position: relative;
}
.social-login .divider::before, .social-login .divider::after {
  content: "";
  position: absolute;
  top: 50%;
  width: 30%;
  height: 1px;
  background-color: hsl(210, 10.3448275862%, 102.7450980392%);
}
.social-login .divider::before {
  left: 0;
}
.social-login .divider::after {
  right: 0;
}
.social-login .social-buttons {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-bottom: 1.5rem;
}
.social-login .social-btn {
  display: inline-block;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  font-weight: 700;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  outline: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  gap: 0.5rem;
  background-color: #f8f9fa;
  border: 1px solid hsl(210, 10.3448275862%, 102.7450980392%);
  color: #333;
}
.social-login .social-btn:disabled {
  opacity: 0.65;
  cursor: not-allowed;
}
.social-login .social-btn:hover {
  background-color: hsl(210, 10.3448275862%, 117.7450980392%);
}
.social-login .social-btn.google {
  color: #DB4437;
}
.social-login .social-btn.facebook {
  color: #4267B2;
}

.signup-prompt {
  text-align: center;
  color: #333;
  margin: 0;
}
.signup-prompt a {
  color: #d4af37;
  text-decoration: none;
  font-weight: 700;
}
.signup-prompt a:hover {
  text-decoration: underline;
}

@media (min-width: 720px) {
  .auth-side {
    display: none;
  }
  .auth-content {
    padding: 1.5rem;
  }
}
@media (min-width: 540px) {
  .social-login .social-buttons {
    grid-template-columns: 1fr;
  }
}