// src/scss/abstracts/_mixins.scss
@use 'sass:math';
@use 'sass:color';
@use 'sass:map';
@use 'variables' as vars;
@use 'functions' as fn;


// Media Queries
@mixin respond-to($breakpoint) {
    @if map.has-key(vars.$container-max-widths, $breakpoint) {
        @media (min-width: map.get(vars.$container-max-widths, $breakpoint)) {
            @content;
        }
    } @else if $breakpoint == xs {
        @media (max-width: #{map.get(vars.$container-max-widths, 'sm') - 1}) {
            @content;
        }
    } @else {
        @warn "No breakpoint found for '#{$breakpoint}'. "
                + "Available breakpoints are: #{map.keys(vars.$container-max-widths)}.";
    }
}

// Flexbox
@mixin flex($direction: row, $justify: flex-start, $align: stretch, $wrap: nowrap) {
    display: flex;
    flex-direction: $direction;
    justify-content: $justify;
    align-items: $align;
    flex-wrap: $wrap;
}

@mixin flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

// Typography
@mixin font-size($size) {
    font-size: $size;
    line-height: math.div($size * 3, 2);
}

@mixin heading($size, $weight: vars.$font-weight-bold) {
    font-family: vars.$font-primary;
    font-size: $size;
    font-weight: $weight;
    line-height: 1.2;
}

// Transitions & Animations
@mixin transition($property: all, $duration: 0.3s, $timing: ease) {
    transition: $property $duration $timing;
}

@mixin hover-lift($distance: -5px) {
    transition: transform 0.3s ease;
    &:hover {
        transform: translateY($distance);
    }
}

@mixin hover-scale($scale: 1.05) {
    transition: transform 0.3s ease;
    &:hover {
        transform: scale($scale);
    }
}

// Cards
@mixin card-base {
    background: vars.$light-color; // Changed from vars.$white to vars.$light-color
    border-radius: vars.$border-radius;
    box-shadow: vars.$box-shadow;
    overflow: hidden;
}

// Images
@mixin img-fluid {
    max-width: 100%;
    height: auto;
}

@mixin background-cover {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

// Position
@mixin absolute-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@mixin fixed-full {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

// Grid
@mixin grid($columns: 1, $gap: vars.$spacing-md) {
    display: grid;
    grid-template-columns: repeat($columns, 1fr);
    gap: $gap;
}

// Overlays
@mixin overlay($base-color: vars.$dark-color) {
    position: relative;
    
    &::before {
        content: '';
        position: absolute;
        inset: 0;
        background-color: color.adjust($base-color, $alpha: -0.5);
    }
}

// Container
@mixin container {
    width: 100%;
    padding-inline: vars.$spacing-md;
    margin-inline: auto;

    @each $breakpoint, $width in vars.$container-max-widths {
        @include respond-to($breakpoint) {
            max-width: $width;
        }
    }
}

// Text Truncate
@mixin text-truncate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

// Button Base
@mixin button-base {
    display: inline-block;
    padding: vars.$spacing-sm vars.$spacing-md;
    border-radius: vars.$border-radius;
    font-weight: vars.$font-weight-bold;
    text-align: center;
    cursor: pointer;
    transition: vars.$transition-base;
    border: none;
    outline: none;

    &:disabled {
        opacity: 0.65;
        cursor: not-allowed;
    }
}

// Button Variant
@mixin button-variant($background, $text-color: vars.$light-color) {
    background-color: $background;
    color: $text-color;

    &:hover,
    &:focus {
        background-color: color.adjust($background, $lightness: -10%);
    }
}

// Scrollbar
@mixin custom-scrollbar($width: 6px, $track-color: vars.$light-color, $thumb-color: vars.$accent-color) {
    &::-webkit-scrollbar {
        width: $width;
    }
    
    &::-webkit-scrollbar-track {
        background: $track-color;
    }
    
    &::-webkit-scrollbar-thumb {
        background: $thumb-color;
        border-radius: math.div($width, 2);
    }
}