// src/scss/pages/_home.scss
@use '../abstracts/variables' as vars;
@use '../abstracts/mixins' as mix;
@use 'sass:color';

.home {
    // Hero Section
    .hero-section {
      height: 100vh;
      min-height: 600px;
      background: linear-gradient(rgba(vars.$dark-color, 0.5), rgba(vars.$dark-color, 0.5)),
                  url('/src/assets/images/backgrounds/bg1.avif') no-repeat center center;
      background-size: cover;
      background-attachment: fixed;
      display: flex;
      align-items: center;
      text-align: center;
      color: vars.$light-color;
  
      .container {
        max-width: 800px;
      }
  
      h1 {
        font-size: 3.5rem;
        margin-bottom: vars.$spacing-md;
        animation: fadeInDown 1s ease;
  
        @include mix.respond-to(md) {
          font-size: 2.5rem;
        }
      }
  
      p {
        font-size: vars.$font-size-lg;
        margin-bottom: vars.$spacing-lg;
        animation: fadeInUp 1s ease 0.3s;
        animation-fill-mode: both;
      }
  
      .btn {
        animation: fadeIn 1s ease 0.6s;
        animation-fill-mode: both;
      }
    }
  
    // Featured Collections
    .featured-collections {
      padding: vars.$spacing-xl 0;
      background-color: vars.$light-color;
  
      .section-title {
        text-align: center;
        margin-bottom: vars.$spacing-xl;
  
        h2 {
          font-size: 2.5rem;
          margin-bottom: vars.$spacing-sm;
        }
  
        p {
          color: rgba(vars.$text-color, 0.7);
          max-width: 600px;
          margin: 0 auto;
        }
      }
  
      .collection-grid {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        gap: vars.$spacing-lg;
      }
    }
  
    // About Section
    .about-section {
      padding: vars.$spacing-xl 0;
      background-color: rgba(vars.$secondary-color, 0.05);
  
      .about-content {
        h2 {
          font-size: 2.5rem;
          margin-bottom: vars.$spacing-md;
        }
  
        p {
          margin-bottom: vars.$spacing-md;
          color: rgba(vars.$text-color, 0.8);
        }
      }
  
      .about-image {
        position: relative;
        
        img {
          border-radius: vars.$border-radius;
          box-shadow: vars.$box-shadow-lg;
        }
  
        &::before {
          content: '';
          position: absolute;
          top: calc(-1 * vars.$spacing-md);
          left: calc(-1 * vars.$spacing-md);
          width: 100%;
          height: 100%;
          border: 2px solid vars.$secondary-color;
          border-radius: vars.$border-radius;
          z-index: -1;
        }
      }
    }
  
    // Testimonials Section
    .testimonials-section {
      padding: vars.$spacing-xl 0;
      background: linear-gradient(rgba(vars.$dark-color, 0.9), rgba(vars.$dark-color, 0.9)),
                  url('/src/assets/images/backgrounds/testimonials-bg.jpg') no-repeat center center;
      background-size: cover;
      background-attachment: fixed;
      color: vars.$light-color;
  
      .section-title {
        text-align: center;
        margin-bottom: vars.$spacing-xl;
      }
  
      .testimonial-slider {
        .testimonial-item {
          text-align: center;
          padding: vars.$spacing-lg;
  
          .testimonial-image {
            width: 100px;
            height: 100px;
            border-radius: 50%;
            margin: 0 auto vars.$spacing-md;
            border: 3px solid vars.$secondary-color;
            overflow: hidden;
  
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }
  
          .testimonial-text {
            font-style: italic;
            margin-bottom: vars.$spacing-md;
          }
  
          .testimonial-author {
            color: vars.$secondary-color;
            font-weight: vars.$font-weight-bold;
          }
        }
      }
    }
  
    // Newsletter Section
    .newsletter-section {
      padding: vars.$spacing-xl 0;
      background-color: vars.$primary-color;
      color: vars.$light-color;
      text-align: center;
  
      h2 {
        margin-bottom: vars.$spacing-md;
      }
  
      p {
        max-width: 600px;
        margin: 0 auto vars.$spacing-lg;
        color: rgba(vars.$light-color, 0.8);
      }
  
      form {
        max-width: 500px;
        margin: 0 auto;
      }
    }
  }
  
  // Animations
  @keyframes fadeInDown {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }